import { store } from "../storage/storage"
import { finfal_rc } from "../backend_api";
import { registerTable } from "../storage/storage";
import { createTableSlice } from "../storage/slices/tableSlice";
import { addTableReducer } from "../storage/storage";
import { CommonTableController } from "./common_table_controller";

class ProfileTableController extends CommonTableController {
    constructor() {
        super("profiles")
        this.set_filters({
            'pname_filter': null,
            "sname_filter": null,
        })
    }
    get_keys = () => {
        return ["id", "profile_name", "subject_name", "activity", "created_at"]
    }

    get_content() {
        let data = super.get_content()
        for (let i in data) {
            let item = data[i]
            let activity = ""
            for (let role in item["activity"]) {
                if (item["activity"][role]) {
                    if (activity == "") {
                        activity = role
                    }
                    else {
                        activity = activity + ", " + role
                    }
                }
            }
            data[i]["activity"] = activity
        }
        return data
    }


    update_content = () => {
        finfal_rc.get_profiles(
            this.filters["pname_filter"],
            this.filters["sname_filter"],
            this.filters["offset"],
            this.filters["count"],
        ).then((data) => {
            store.dispatch(this.actions.set_content(data))
            let selected_item = this.get_selected_item()
            if ("id" in selected_item) {
                finfal_rc.get_profile_by_id(selected_item["id"]).then((item_info) => { this.on_select(item_info) })

            }
        })
    }

    get_available_actions = () => {
        let selected_item = this.get_selected_item()
        console.log("ProfileTableController.get_available_actions")
        if (selected_item == null) return []
        if (!("id" in selected_item)) return []
        return ["view", "edit", "delete", "make_active"]
    }
}

export const profile_table_controller = new ProfileTableController()