import React from "react";
import { ReduxMemoryAccess } from "../widgets/memory_redux_access";
import { useSelector, useDispatch } from 'react-redux';
import { login, logout, set_token } from "../../static/storage/slices/authSlice";
import { config_loader } from "../../static/config_loader";
import { finfal_rc } from "../../static/backend_api";
import { auth_controller } from "../../static/controllers/auth_controller";
import { modal_controller } from "../../static/controllers/modal_controller";
import { status_controller } from "../../static/controllers/ca_status_controller";
import { InputString } from "../widgets/inputs";
import { locales } from "../../static/locales";
import '../../styles/Login.css'

function LoginWindow() {
    const token = useSelector((state) => state.auth.token);
    const dispatch = useDispatch();
    return (
        <div>
            <label>LoginWindow: {token} </label>
            <button onClick={(e) => { auth_controller.auth("string", "string") }}> log in</button>
        </div>
    )
}
//() => dispatch(login())

export class LoginPage extends React.Component {
    constructor() {
        super()
        this.state = {
            "login": "string",
            "password": "string",
            "dup_password": "string",
            "error_message": ""
        }
        status_controller.update_status()
        console.log("LoginPage constructor")
    }
    componentDidMount(){
        console.log("LoginPage componentDidMount")
        
    }
    onLogin = () => {
        auth_controller.auth(this.state.login, this.state.password, this.onLoginSuccess, this.onLoginFailure)
    }

    onLoginSuccess = () => {
        this.setState({ "error_message": "" })
    }

    onLoginFailure = () => {
        this.setState({ "error_message": "wrong password or login" })
    }

    onInitiate = () => {
        finfal_rc.post_ca_initiate(this.state.login, this.state.password).then((e)=>{this.onLogin()})
    }

    on_password_validation = (value) => {
        if (this.state.dup_password !== this.state.password) {
            return false
        }
        else {
            return true
        }
    }

    get_warning_label = () => {
        if (this.state.error_message != "") {
            return <label>{locales.get(this.state.error_message)}</label>
        }
        if (this.on_password_validation()) {
            return <span></span>
        }
        return <label>passwords do not match</label>
    }

    get_login_page = () => {
        return <div className="LoginPage">
            <img src='imgs/logo_rus.jpg' />
            <label className="CA_name_label">{locales.get("CA_product_name")}</label>
            <InputString value={this.state.login} onChange={(value) => { this.setState({ "login": value }) }} label={locales.get("login")} />
            <InputString value={this.state.password} onChange={(value) => { this.setState({ "password": value }) }} label={locales.get("password")} />
            <InputString value={this.state.dup_password} onChange={(value) => { this.setState({ "dup_password": value }) }} label={locales.get("dup_password")} />
            {this.get_warning_label()}
            <button disabled={!this.on_password_validation()} onClick={this.onLogin}>{locales.get("do_login")}</button>
        </div>
    }

    get_initiation_page = () => {
        return <div className="LoginPage">
            <img src='imgs/logo_rus.jpg' />
            <label className="CA_name_label">{locales.get("CA_product_name")}</label>
            <InputString value={this.state.login} onChange={(value) => { this.setState({ "login": value }) }} label={locales.get("login")} />
            <InputString value={this.state.password} onChange={(value) => { this.setState({ "password": value }) }} label={locales.get("password")} />
            <InputString value={this.state.dup_password} onChange={(value) => { this.setState({ "dup_password": value }) }} label={locales.get("dup_password")} />
            {this.get_warning_label()}
            <button disabled={!this.on_password_validation()} onClick={this.onInitiate}>{locales.get("initiate")}</button>
        </div>
    }

    get_unavailable_page = () => {
        return <div className="LoginPage">
            <img src='imgs/logo_rus.jpg' />
            <label className="CA_name_label">{locales.get("CA_product_name")}</label>
            <label>{locales.get("CA is unavailable now")}</label>
        </div>
    }
    render() {
        if (status_controller.is_CA_stable()) {
            if (status_controller.is_CA_initialized()) {
                return this.get_login_page()
            }
            else {
                return this.get_initiation_page()
            }
        }
        return this.get_unavailable_page()

    }
}