import { createSlice } from '@reduxjs/toolkit';
import { act } from 'react';

const statusSlice = createSlice({
    name: 'status',
    initialState: {
        "is_CA_stable": true,
        "is_CA_initialized": true,
        "crypto": {
            "is_accessable": false,
            "is_auth_data_valid": false,
        },
        "db": {
            "is_accessable": false,
            "is_auth_data_valid": false,
        },
        "has_active_profile": {
            "common": false,
            "root": false,
        },
        "has_active_scert": {
            "exists": false,
            "is_pin_code_valid": false
        }
    },
    reducers: {
        set_status(state, action) {
            for(let key in action.payload){
                state[key]=action.payload[key]
            }
        },
        set_unstable(state, action){
            state["is_CA_stable"]=false
        }
    },
});

export const { set_status, set_unstable } = statusSlice.actions;
export default statusSlice.reducer;  // default export!