import React from "react";
import { InputString } from "../widgets/inputs";
import { finfal_rc } from "../../static/backend_api";
import { locales } from "../../static/locales";

export class SettingsPage extends React.Component {
    constructor() {
        super()
        this.state = {
            "db":{
                "url":"",
                "login":"",
                "password":"",
            },            
            "crypto":{
                "url":"",
                "login":"",
                "password":"",
            }
        }
    }
    componentDidMount(){
        finfal_rc.get_ca_config().then((config) =>{this.setState(config)}).catch((e)=>{})
    }

    get_block = (section_name) =>{
        let result = [<label>{locales.get(section_name)}</label>]
        let params_list = ["url", "login", "password"]
        for (let i in params_list){
            let key = params_list[i]
            result.push(
                <InputString
                label={key} 
                value = {this.state[section_name][key]}
                onChange = {(value)=>{
                    let tmp = Object.assign({},this.state[section_name])
                    tmp[key] = value
                    this.setState({[section_name]:tmp})
                }}/>
            )
        }
        return <div>
            {result}
        </div>

    }

    on_save_config = () =>{
        finfal_rc.post_ca_config(this.state)
    }

    render() {
        return <div>
            {this.get_block("db")}
            {this.get_block("crypto")}
            <button onClick={this.on_save_config}>{locales.get("save")}</button>
        </div>
    }
}