import React from "react";
import '../../styles/MainPage.css'
import { ReduxMemoryAccess } from "../widgets/memory_redux_access";
import { ProfilesPage } from "./profiles";
import { ScertsPage } from "./scerts";
import { CsrsPage } from "./csrs";
import { CertsPage } from "./certs";
import { CrlsPage } from "./crls";
import { SettingsPage } from "./settings";
import { locales } from "../../static/locales";
import { logout } from "../../static/storage/slices/authSlice";
import { navigation_controller } from "../../static/controllers/navigation_controller";
import { store} from "../../static/storage/storage";
import { MyProfilePage } from "./myprofile";
import { StatusViewer } from "../widgets/status_viewer";
import { UsersPage } from "./users";
import { auth_controller } from "../../static/controllers/auth_controller";
import { LogsPage } from "./logs";
import { Header } from "./header";

class Navigation extends React.Component{
    get_tabs_list = () =>{
        let tabs_list = navigation_controller.get_tabs_list(auth_controller.get_role())
        let tabs_list_obj = []
        for (let i in tabs_list){
            let tab_name = tabs_list[i]
            let class_name = ""
            if (navigation_controller.is_tab_active(tab_name)){
                class_name = "tab_active"
            }
            tabs_list_obj.push(<li 
                className={class_name} 
                onClick={(e)=>{navigation_controller.set_tab_active(tab_name)}}>
                    {locales.get(tab_name)}</li>)
        }
        return tabs_list_obj

    }
    render() { 
        return <div className="Navigation">
            {this.get_tabs_list()}
        </div>
        
    }
}

class ContentZone extends React.Component {
    get_active_content = ()=>{
        switch (navigation_controller.get_current_active_tab()){
            case 'profiles':{
                return <ProfilesPage></ProfilesPage>
            }
            case 'scerts':{
                return <ScertsPage></ScertsPage>
            }
            case 'csrs':{
                return <CsrsPage/>
            }
            case 'certs':{
                return <CertsPage/>
            }
            case 'crls':{
                return <CrlsPage/>
            }
            case 'settings':{
                return <SettingsPage/>
            }
            case 'my_profile':{
                return <MyProfilePage/>
            }
            case 'administration':{
                return <UsersPage/>
            }
            case 'logs':{
                return <LogsPage/>
            }
        }
    }
    render() { 
        return <div className="ContentZone">{this.get_active_content()}</div>
    }
}

class Footer extends React.Component {
    render() { 
        return <div>
            
        </div>
    }
}

export class MainPage extends React.Component {
    render() {
        return <div className="MainPage">
            <Header></Header>
            <div className="MainPage_content">
                <Navigation></Navigation>
                <ContentZone></ContentZone>
            </div>
            <Footer></Footer>
        </div>
    }
}