import { store } from "../storage/storage"
import { finfal_rc } from "../backend_api";
import { registerTable } from "../storage/storage";
import { createTableSlice } from "../storage/slices/tableSlice";
import { addTableReducer } from "../storage/storage";
import { CommonTableController } from "./common_table_controller";

class CsrTableController extends CommonTableController{
    constructor(){
        super("csrs")
        this.set_filters({
            "sname_filter":null,
            "status": null
        })
    }
    get_keys = () =>{
        return ["id", "subject_name", "scert_subject_name", "status", "created_at"]
    }

    get_content(){
        let data = super.get_content()
        for(let i in data){
            let item = data[i]
            if (item["is_active"]){
                item["is_active"] = "true"
            }
            else{
                item["is_active"] = "false"
            }
        }
        return data
    }


    on_select = (item) =>{
        finfal_rc.get_csr_by_id(item.id).then((item)=>{store.dispatch(this.actions.select_item(item))})
    }

    update_content = () =>{
        let time_filters = this.get_time_intervals()
        finfal_rc.get_csrs(
            time_filters["not_before"],
            time_filters["not_after"],
            this.filters["sname_filter"],
            this.filters["status"],
            this.filters["offset"],
            this.filters["count"],
        ).then((data) => {
            store.dispatch(this.actions.set_content(data))
          })
    }

    get_available_actions = () =>{
        let selected_item = this.get_selected_item()
        console.log("CsrTableController.get_available_actions")
        if (selected_item==null) return []
        if (!("id" in selected_item)) return []
        return ["view", "edit", "delete", "make_active"]
    }
}

export const csr_table_controller = new CsrTableController()