import { finfal_rc } from "../backend_api"
import { store } from "../storage/storage"
import { set_status, set_unstable} from "../storage/slices/statusSlice"
class StatusController{
    constructor(){
        this.status_data = store.getState().status
        this.unsubscribe = store.subscribe(() => {
                this.status_data = store.getState().status
        })
        this.update_status()
        setInterval(this.update_status, 5000)
    }
    
    get_status = ()=>{
        return this.status_data
    }

    update_status = () =>{
        finfal_rc.get_ca_status().then((status)=>{
            store.dispatch(set_status(status))
        })
        .catch((e)=>{
            this.set_unavailable_status()
            console.log("update_status err",e)
        })
    }

    set_unavailable_status = () =>{
        store.dispatch(set_unstable())
    }

    is_CA_stable = () =>{
        return this.get_status()["is_CA_stable"]
    }

    is_CA_initialized = () =>{
        return this.get_status()["is_CA_initialized"]
    }

    /*
    initialState: {
        "is_CA_initialized": true,
        "crypto": {
            "is_accessable": false,
            "is_auth_data_valid": false,
        },
        "db": {
            "is_accessable": false,
            "is_auth_data_valid": false,
        },
        "has_active_profile": {
            "common": false,
            "root": false,
        },
        "has_active_scert": {
            "exists": false,
            "is_pin_code_valid": false
        }
    },
    */

    get_status_string = () =>{
        let status_string = ""
        let status_dict = this.get_status()
        if (status_dict["is_CA_initialized"] == false) return "CA not initialized"
        if (status_dict["crypto"]["is_accessable"] == false) return "Crypto is not available"
        if (status_dict["crypto"]["is_auth_data_valid"] == false) return "Crypto auth data is not valid"
        if (status_dict["db"]["is_accessable"] == false) return "DB service is not available"
        if (status_dict["db"]["is_auth_data_valid"] == false) return "DB service auth data is not valid"
        if (status_dict["has_active_profile"]["root"] == false) return "No active root profile"
        if (status_dict["has_active_profile"]["common"] == false) return "No active common profile"
        if (status_dict["has_active_scert"]["exists"] == false) return "No active scert"
        if (status_dict["has_active_scert"]["is_pin_code_valid"] == false) return "Active scert has no pincode"
        return "ok"
        
    }
}

export const status_controller = new StatusController()