import { createSlice } from '@reduxjs/toolkit';

let not_before_start = new Date()
not_before_start.setHours(0, 0, 0, 0)

let not_after_start = new Date()
not_after_start.setHours(23, 59, 59, 999)

const timeSlice = createSlice({
  name: 'time',
  initialState: {
    not_before: not_before_start.toISOString().slice(0, 16),
    not_after: not_after_start.toISOString().slice(0, 16)
  },
  reducers: {
    set_lower_time_border(state, action){
        state.not_before = action.payload
    },
    set_upper_time_border(state, action){
        state.not_after = action.payload
    }
  },
});

export const { set_lower_time_border, set_upper_time_border } = timeSlice.actions;
export default timeSlice.reducer;  // default export!