export class ScertsPage extends React.Component {
  constructor() {
    super()
  }

  componentDidMount() {
    this.on_load_scerts()
  }

  on_view_scert = () => {
    let scert_action = <ScertAction mode='view' item={scert_table_controller.get_selected_item()} />
    modal_controller.show(scert_action)
  }

  on_delete_scert = () => {
    let scert_action = <ScertAction mode='delete' item={scert_table_controller.get_selected_item()} />
    modal_controller.show(scert_action)
  }

  on_make_active_scert = () => {
    let scert_action = <ScertAction mode='make_active' item={scert_table_controller.get_selected_item()} />
    modal_controller.show(scert_action)
  }

  on_set_pin = () => {
    let scert_action = <ScertAction mode='set_pin' item={scert_table_controller.get_selected_item()} />
    modal_controller.show(scert_action)
  }

  on_create_scert = () => {
    let scert_editor = <ScertEditor />
    modal_controller.show(scert_editor)
  }

  on_load_scerts = () => {
    console.log("ScertsPage.on_load_scerts")
    scert_table_controller.update_content()
  }

  on_export = () => {
    let item = scert_table_controller.get_selected_item()
    let cert_text = "-----BEGIN CERTIFICATE-----\n" + item.text + "\n-----END CERTIFICATE-----"
    var myblob = new Blob([cert_text], {
      type: 'text/plain'
    });
    let url = window.URL.createObjectURL(myblob);
    let a = document.createElement('a');
    a.href = url;
    a.download = item.subject_name + '.cer';
    a.click();
  }

  get_button_panel = () => {
    let result = []
    result.push(<button onClick={this.on_load_scerts}>{locales.get("UpdateTable")}</button>)
    result.push(<button onClick={this.on_create_scert}>{locales.get("create")}</button>)

    let available_actions = scert_table_controller.get_available_actions()
    console.log("ScertsPage.get_button_panel", available_actions)
    for (let i in available_actions) {
      let action_name = available_actions[i]
      switch (action_name) {
        case "view": {
          result.push(<button onClick={this.on_view_scert}>{locales.get(action_name)}</button>)
          break
        }
        case "delete": {
          result.push(<button onClick={this.on_delete_scert}>{locales.get(action_name)}</button>)
          break
        }
        case "make_active": {
          result.push(<button onClick={this.on_make_active_scert}>{locales.get(action_name)}</button>)
          break
        }
        case "export": {
          result.push(<button onClick={this.on_export}>{locales.get(action_name)}</button>)
          break
        }
        case "set_pin": {
          result.push(<button onClick={this.on_set_pin}>{locales.get(action_name)}</button>)
          break
        }
      }
    }

    return <div className="ActionPanel">{result}</div>
  }

  render() {
    return (<div>
      {this.get_button_panel()}
      <CustomTable
        keys={scert_table_controller.get_keys()}
        content={scert_table_controller.get_content()}
        marks={scert_table_controller.get_selected_idxs()}
        on_item_select={scert_table_controller.on_select}
      />
      <CustomTablePagingButtons
        current_page={scert_table_controller.get_current_page()}
        on_prev_page={scert_table_controller.on_prev_page}
        on_next_page={scert_table_controller.on_next_page}
      />
    </div>)
  }
}

class ScertEditor extends ActionWrapper {
  constructor() {
    super()
    this.state = {
      subject_name: "",
      algorithm: "",
      pin: "11111111"
    }
  }

  on_create = () => {
    finfal_rc.post_scert(this.state.subject_name,
      this.state.algorithm,
      this.state.pin).then((result) => {
        modal_controller.hide()
        scert_table_controller.update_content()
      }).catch((data) => {
        console.log(data["content"])
        this.onError(data["content"])
      })
  }

  on_close = () => {
    modal_controller.hide()
  }

  on_data_validation = () => {
    let validations = [pin_validation(this.state.pin), cant_be_empty(this.state.algorithm), subject_name_validation(this.state.subject_name)]
    for (let i in validations) {
      if (validations[i]["state"] == false) return false
    }
    return true
  }


  get_body = () => {
    return <div>
      <InputString label="subject_name" validation={subject_name_validation} value={this.state.subject_name} onChange={(value) => { this.setState({ "subject_name": value }) }} />
      <InputSelector label="algorithm" validation={cant_be_empty} options={["gostr34102012-gostr34112012-256"]} value={this.state.algorithm} onChange={(value) => { this.setState({ "algorithm": value }) }} />
      <InputString label="pin" validation={pin_validation} value={this.state.pin} onChange={(value) => { this.setState({ "pin": value }) }} />
    </div>
  }

  get_buttons = () => {
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button disabled={!this.on_data_validation()} onClick={this.on_create}>{locales.get("create")}</button>
    </div>

  }

  render() {
    return <div>
      {this.get_body()}
      {this.get_buttons()}
    </div>
  }
}

class ScertAction extends React.Component {
  on_close = () => {
    modal_controller.hide()
  }

  get_action_subwidget = () => {
    switch (this.props.mode) {
      case "view": {
        return <ScertActionView />
      }
      case "delete": {
        return <ScertActionDelete item={this.props.item} />
      }
      case "make_active": {
        return <ScertMakeActive item={this.props.item} />
      }
      case "set_pin": {
        return <ScertSetPin item={this.props.item} />
      }
    }
  }

  render() {
    return (<div className="ScertAction">
      <ScertViwer item={this.props.item} />
      {this.get_action_subwidget()}
    </div>)
  }
}

