import React from "react";
import { InputDatetime } from "./inputs";
import { store } from "../../static/storage/storage";
import { set_lower_time_border, set_upper_time_border } from "../../static/storage/slices/timeSlice";
import '../../styles/filters.css'
export class TimeIntervalSelector extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            time_interval: store.getState().time,
        };
    }

    componentDidMount() {
        this.unsubscribe = store.subscribe(() => {
            this.setState({
                time_interval: store.getState().time,
            });
        });
    }

    set_not_before = (value) =>{
        store.dispatch(set_lower_time_border(value))
        if (this.props.onUpdate){
            this.props.onUpdate()
        }
    }

    set_not_after = (value) =>{
        store.dispatch(set_upper_time_border(value))
        if (this.props.onUpdate){
            this.props.onUpdate()
        }
    }

    log = (value) => {
        console.log(value, typeof (value))
    }


    render() {
        return <div className="TimeIntervalSelector">
            <InputDatetime label={"not_before"} onChange={this.set_not_before} value = {this.state.time_interval.not_before}/>
            <InputDatetime label={"not_after"} onChange={this.set_not_after} value = {this.state.time_interval.not_after}/>
        </div>
    }
}