export class CsrsPage extends React.Component {
  constructor() {
    super()
  }

  componentDidMount() {
    this.on_load_csrs()
  }

  on_view_csr = () => {
    let csr_action = <CsrAction mode='view' item={csr_table_controller.get_selected_item()} />
    modal_controller.show(csr_action)
  }

  on_delete_csr = () => {
    let csr_action = <CsrAction mode='delete' item={csr_table_controller.get_selected_item()} />
    modal_controller.show(csr_action)
  }

  on_make_active_csr = () => {
    let csr_action = <CsrAction mode='make_active' item={csr_table_controller.get_selected_item()} />
    modal_controller.show(csr_action)
  }

  on_upload_csr = () => {
    let csr_editor = <CsrUploader />
    modal_controller.show(csr_editor)
  }

  on_load_csrs = () => {
    console.log("CsrsPage.on_load_csrs")
    csr_table_controller.update_content()
  }

  get_button_panel = () => {
    let result = []
    result.push(<button onClick={this.on_load_csrs}>{locales.get("UpdateTable")}</button>)
    result.push(<button onClick={this.on_upload_csr}>{locales.get("upload_csr")}</button>)

    let available_actions = csr_table_controller.get_available_actions()
    console.log("CsrsPage.get_button_panel", available_actions)
    for (let i in available_actions) {
      let action_name = available_actions[i]
      switch (action_name) {
        case "view": {
          result.push(<button onClick={this.on_view_csr}>{locales.get(action_name)}</button>)
          break
        }
        case "delete": {
          result.push(<button onClick={this.on_delete_csr}>{locales.get(action_name)}</button>)
          break
        }
      }
    }

    return <div className="ActionPanel">{result}</div>
  }

  render() {
    return (<div>
      <TimeIntervalSelector
        onUpdate={this.on_load_csrs} />
      <FiltersLayerWidget
        table_controller={csr_table_controller}
        selector_options={["uploaded", "in_progress", "proceeded"]}
      />
      {this.get_button_panel()}
      <CustomTable
        keys={csr_table_controller.get_keys()}
        content={csr_table_controller.get_content()}
        marks={csr_table_controller.get_selected_idxs()}
        on_item_select={csr_table_controller.on_select}
      />
      <CustomTablePagingButtons
        current_page={csr_table_controller.get_current_page()}
        on_prev_page={csr_table_controller.on_prev_page}
        on_next_page={csr_table_controller.on_next_page}
      />
    </div>)
  }
}

export class CustomScertInputSelector extends InputSelector {
  get_value_from_event = (e) => {
    return e.target.value
  }

  get_options = () => {
    let options_list = [<option key={""} value={null}> ... </option>]
    for (let k in this.props.options) {
      let option = this.props.options[k]
      console.log("CustomScertInputSelector", option)
      options_list.push(<option key={option} value={option["id"]}> {locales.get(option["subject_name"])} </option>)
    }
    return options_list
  }
  get_content_line = () => {
    return <div className="content_line">
      <label>{locales.get(this.props.label)}</label>
      <select key={this.state.triggerkey}
        onChange={this.onChange}>{this.get_options()}</select>

    </div>
  }

}

class CsrUploader extends ActionWrapper {
  constructor() {
    super()
    this.state = {
      csr_texts: [],
      csr_filenames: [],
      scert_id: null
    }
  }

  on_upload = () => {

    for (let i in this.state.csr_texts) {
      let file_content = file_encoder.get_csr_text_from_raw_filecontent(this.state.csr_texts[i])
      finfal_rc.post_csr(file_content, this.state.scert_id)
        .then((result) => {
          modal_controller.hide()
          csr_table_controller.update_content()
        }).catch((data) => {
          console.log(data["content"])
          this.onError(data["content"])
        })
    }

  }

  on_close = () => {
    modal_controller.hide()
  }


  uploadCsrContent = (event) => {
    ////console.log("event.target.files", event.target.files,)
    let csr_texts = []
    let csr_filenames = []
    for (var i = 0; i < event.target.files.length; i++) {
      ////console.log("i", i, typeof (i))
      let reader = new FileReader()
      reader.readAsDataURL(event.target.files[i]);
      reader.onload = () => {
        csr_texts.push(reader.result)
      }
      csr_filenames.push(event.target.files[i]['name'])

    }

    this.setState({
      'csr_texts': csr_texts,
      'csr_filenames': csr_filenames
    })

  }

  get_body = () => {
    return <div className="CsrUploader_body">
      <input type="file" accept=".csr" onChange={this.uploadCsrContent} multiple />
      <CustomScertInputSelector
        label="scert_select_for_csr"
        onChange={(scert_id) => {
          this.setState({ "scert_id": scert_id })
        }}
        options={scert_table_controller.get_content()} />
    </div>
  }

  get_buttons = () => {
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_upload}>{locales.get("upload")}</button>
    </div>

  }

  render() {
    return <div>
      {this.get_body()}
      {this.get_buttons()}
    </div>
  }
}

class CsrAction extends React.Component {
  on_close = () => {
    modal_controller.hide()
  }

  get_action_subwidget = () => {
    switch (this.props.mode) {
      case "view": {
        return <CsrActionView />
      }
      case "delete": {
        return <CsrActionDelete item={this.props.item} />
      }
      case "make_active": {
        return <CsrMakeActive item={this.props.item} />
      }
    }
  }

  render() {
    return (<div className="CsrAction">
      <CsrViwer item={this.props.item} />
      {this.get_action_subwidget()}
    </div>)
  }
}

