export class CsrViwer extends React.Component {
  constructor() {
    super()
    this.state = {
      subject_name: "",
      pkey_algorithm: "",
      pkey_value: "",
    }
  }

  componentDidMount() {
    extsx509v3_controller.upload_from_item(this.props.item.exts)
    this.setState({
      issuer_name: this.props.item.issuer_name,
      subject_name: this.props.item.subject_name,
      pkey_algorithm: this.props.item.pkey_algorithm,
      pkey_value: this.props.item.pkey_value,
    })
  }

  render() {
    return <div>
      <InputString disabled={true} label="subject_name" value={this.state.subject_name} />
      <InputString disabled={true} label="pkey_algorithm" value={this.state.pkey_algorithm} />
      <PKeyViewer value={this.state.pkey_value} />
    </div>
  }

}