import { createSlice } from '@reduxjs/toolkit';

const navigationSlice = createSlice({
  name: 'navigation',
  initialState: {
    current_tab: "profiles",
    tabs_list: 
    {
      "admin":["profiles", "scerts", "csrs", "certs", "crls", "settings", "administration", "logs", "my_profile"],
      "user":["profiles", "scerts", "csrs", "certs", "crls", "logs", "my_profile"]
    }
    
  },
  reducers: {
    set_current_tab(state, action){
        state.current_tab = action.payload
    },
  },
});

export const {set_current_tab} = navigationSlice.actions;
export default navigationSlice.reducer;  // default export!