export class ProfilesPage extends React.Component {
  constructor() {
    super()
  }

  componentDidMount() {
    this.on_load_profiles()
  }

  on_view_profile = () => {
    let profile_action = <ProfileAction mode='view' item={profile_table_controller.get_selected_item()} />
    modal_controller.show(profile_action)
  }

  on_delete_profile = () => {
    let profile_action = <ProfileAction mode='delete' item={profile_table_controller.get_selected_item()} />
    modal_controller.show(profile_action)
  }

  on_make_active_profile = () => {
    let profile_action = <ProfileAction mode='make_active' item={profile_table_controller.get_selected_item()} />
    modal_controller.show(profile_action)
  }

  on_edit_profile = () => {
    let profile_editor = <ProfileEditor mode="editor" item={profile_table_controller.get_selected_item()} />
    modal_controller.show(profile_editor)
  }

  on_create_profile = () => {
    let profile_editor = <ProfileEditor mode="creator" />
    modal_controller.show(profile_editor)
  }

  on_load_profiles = () => {
    console.log("ProfilesPage.on_load_profiles")
    profile_table_controller.update_content()
  }

  get_button_panel = () => {
    let result = []
    result.push(<button onClick={this.on_load_profiles}>{locales.get("UpdateTable")}</button>)
    result.push(<button onClick={this.on_create_profile}>{locales.get("create")}</button>)

    let available_actions = profile_table_controller.get_available_actions()
    console.log("ProfilesPage.get_button_panel", available_actions)
    for (let i in available_actions) {
      let action_name = available_actions[i]
      switch (action_name) {
        case "view": {
          result.push(<button onClick={this.on_view_profile}>{locales.get(action_name)}</button>)
          break
        }
        case "edit": {
          result.push(<button onClick={this.on_edit_profile}>{locales.get(action_name)}</button>)
          break
        }
        case "delete": {
          result.push(<button onClick={this.on_delete_profile}>{locales.get(action_name)}</button>)
          break
        }
        case "make_active": {
          result.push(<button onClick={this.on_make_active_profile}>{locales.get(action_name)}</button>)
          break
        }
      }
    }

    return <div className="ActionPanel">{result}</div>
  }

  render() {
    return (<div>
      {this.get_button_panel()}
      <CustomTable
        keys={profile_table_controller.get_keys()}
        content={profile_table_controller.get_content()}
        marks={profile_table_controller.get_selected_idxs()}
        on_item_select={profile_table_controller.on_select}
      />
      <CustomTablePagingButtons
        current_page={profile_table_controller.get_current_page()}
        on_prev_page={profile_table_controller.on_prev_page}
        on_next_page={profile_table_controller.on_next_page}
      />
    </div>)
  }
}

class ProfileEditor extends React.Component {
  constructor() {
    super()
    this.state = {
      profile_name: "newProfile",
      subject_name: "SN=newProfile",
      duration: 360,
      key_usage: [],
      exts: {},
    }
  }

  componentDidMount() {
    extsx509v3_controller.clear()
    if (this.props.mode == "editor") {
      console.log("ProfileEditor.editor")
      extsx509v3_controller.upload_from_item(this.props.item.exts)
      this.setState({
        profile_name: this.props.item.profile_name,
        subject_name: this.props.item.subject_name,
        duration: this.props.item.duration,
        key_usage: this.props.item.key_usage,
        exts: this.props.item.exts,
      })

    }
  }



  on_create = () => {
    if (this.props.mode == "editor") {
      let new_exts = extsx509v3_controller.export_to_item()
      finfal_rc.update_profile(this.props.item.id,
        this.state.profile_name,
        this.state.subject_name,
        this.state.duration,
        this.state.key_usage,
        new_exts
      ).then((result) => {
        modal_controller.hide()
        profile_table_controller.update_content()
      })
    }
    else if (this.props.mode == "creator") {
      let new_exts = extsx509v3_controller.export_to_item()
      finfal_rc.post_profile(
        this.state.profile_name,
        this.state.subject_name,
        this.state.duration,
        this.state.key_usage,
        new_exts
      ).then((result) => {
        modal_controller.hide()
        profile_table_controller.update_content()
      }).catch((e) => { console.log("ERROR", e, e.code) })
    }
  }

  on_close = () => {
    modal_controller.hide()
  }

  set_key_usage_key = (key, state) => {
    let current_state = this.get_key_usage_key(key)
    if (current_state != state) {
      let tmp_key_usage = []
      for (let tmp_idx in this.state.key_usage) {
        tmp_key_usage.push(this.state.key_usage[tmp_idx])
      }
      console.log("set_key_usage_key", tmp_key_usage, key)
      if (state) { //if we want to add keyusage

        tmp_key_usage.push(key)
      }
      else {
        tmp_key_usage = tmp_key_usage.filter(item => item !== key)
      }
      this.setState({ "key_usage": tmp_key_usage })
    }
  }

  set_key_usage_array = (keys) =>{
    this.setState({ "key_usage": keys })
  }

  get_key_usage_key = (key) => {
    if (this.state.key_usage.includes(key)) {
      return true
    }
    return false
  }

  get_key_usage_selector = () => {
    return <SelectorFilter 
    label = {locales.get("key_usage")}
    value = {this.state.key_usage}
    onChange={this.set_key_usage_array}
    options = {["digitalSignature",
      "nonRepudiation",
      "keyEncipherment",
      "dataEncipherment",
      "keyAgreement",
      "keyCertSign",
      "crlSign",
      "encipherOnly",
      "decipherOnly"]}/>
    /*let key_usage_list = ["digitalSignature",
      "nonRepudiation",
      "keyEncipherment",
      "dataEncipherment",
      "keyAgreement",
      "keyCertSign",
      "crlSign",
      "encipherOnly",
      "decipherOnly"]
    let result = []
    for (let i in key_usage_list) {
      let label = key_usage_list[i]
      result.push(<InputCheckbox label={label}
        value={this.get_key_usage_key(label)}
        onChange={(value) => { this.set_key_usage_key(label, value) }} />)
    }*/
    //return <div>{result}</div>
  }

  get_exts_constructor = () => {
    return <ExtsX509v3_constructor
      value={this.state.profile_name}
      onChange={(value) => { this.setState({ "profile_name": value }) }} />
  }

  get_body = () => {
    return <div>
      <InputString label="profile_name" value={this.state.profile_name} onChange={(value) => { this.setState({ "profile_name": value }) }} />
      <InputString label="subject_name" validation={subject_name_validation} value={this.state.subject_name} onChange={(value) => { this.setState({ "subject_name": value }) }} />
      <InputInt label="duration" value={this.state.duration} onChange={(value) => { this.setState({ "duration": value }) }} />
      {this.get_key_usage_selector()}
      {this.get_exts_constructor()}
    </div>
  }

  get_buttons = () => {
    if (this.props.mode == "creator") {
      return <div className="modal_action_btn_panel">
        <button onClick={this.on_close}>{locales.get("close")}</button>
        <button onClick={this.on_create}>{locales.get("create")}</button>
      </div>
    }
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_create}>{locales.get("update_profile")}</button>
    </div>
  }

  render() {
    return <div>
      {this.get_body()}
      {this.get_buttons()}
    </div>
  }
}

class ProfileAction extends React.Component {
  on_close = () => {
    modal_controller.hide()
  }

  get_action_subwidget = () => {
    switch (this.props.mode) {
      case "view": {
        return <ProfileActionView />
      }
      case "delete": {
        return <ProfileActionDelete item={this.props.item} />
      }
      case "make_active": {
        return <ProfileMakeActive item={this.props.item} />
      }
    }
  }

  render() {
    return (<div className="ProfileAction">
      <ProfileViwer item={this.props.item} />
      {this.get_action_subwidget()}
    </div>)
  }
}

