import { createSlice } from '@reduxjs/toolkit';

//keys privateKeyUsagePeriod

const extsSlice = createSlice({
  name: 'exts',
  initialState: {
    "privateKeyUsagePeriod": {
      "is_active": false,
      "params": {}
    }
    /*"ECPmethods": {
      "is_active": false,
      "params": {}
    },
    "applicationCertPolicies": {
      "is_active": false,
      "params": {}
    },
    "authorityInfoAccess": {
      "is_active": false,
      "params": {}
    }*/
  },
  reducers: {
    create_ext(state, action) {
      console.log("create_ext", action.payload)
      const ext_name = action.payload;
      state[ext_name] = {
        "is_active": false,
        "params": {}
      }
    },
    clear_exts(state, action) {
      for (let key in state) {
        console.log(key)
        state[key] = {
          "is_active": false,
          "params": {}
        }
      }
    },
    set_active(state, action) {
      const { ext_name, is_active } = action.payload;
      state[ext_name]["is_active"] = is_active
    },

    set_params(state, action) {
      const { ext_name, ext_params } = action.payload;
      state[ext_name]["params"] = Object.assign({}, ext_params)
    }
  },
});

export const { set_active, set_params, create_ext, clear_exts } = extsSlice.actions;
export default extsSlice.reducer;  // default export!