class FileEncoder {
    constructor() {

    }

    get_csr_text_from_raw_filecontent(filecontent) {
        console.log(filecontent)
        let csr_b64 = filecontent.split("data:application/octet-stream;base64,")[1]
        let csr_bytes = atob(csr_b64)
        if (csr_bytes.includes("-----BEGIN CERTIFICATE REQEST-----")) {
            csr_bytes = csr_bytes.replace('\r', '')
            let fragments = csr_bytes.split('\n')

            let result_text = ""
            for (let j in fragments) {
                if (!fragments[j].includes("-----BEGIN CERTIFICATE REQEST-----")){
                    if (!fragments[j].includes("-----END CERTIFICATE REQEST-----")){
                        result_text = result_text + fragments[j]
                    }
                }
            }
            result_text = result_text.replace('\r', '')
            return result_text

        }

    }
}

export const file_encoder = new FileEncoder()