import React from "react";
import { CustomTable, CustomTablePagingButtons } from "../widgets/table";

import { useSelector, useDispatch } from 'react-redux';
import { ReduxMemoryAccess } from "../widgets/memory_redux_access";
import { store } from "../../static/storage/storage";
import { finfal_rc } from "../../static/backend_api";
import { InputString, InputInt, InputCheckbox, InputSelector } from "../widgets/inputs";
import { StringViewer, KeyUsageViewer } from "../widgets/outputs";
import { cert_table_controller } from "../../static/controllers/cert_table_controller";
import { modal_controller } from "../../static/controllers/modal_controller";
import { locales } from "../../static/locales";
import { ExtsX509v3_constructor } from "../widgets/input_exts_constructor";
import { extsx509v3_controller } from "../../static/controllers/exts_controller";
import { subject_name_validation } from "../../static/validators";
import { ErrorMessage } from "../widgets/error_popup";
import { ActionWrapper } from "../widgets/action_wrapper";
import { TimeIntervalSelector } from "../widgets/time_interval_selector";
import { FiltersLayerWidget } from "../widgets/filters_layer_widget";
import { PKeyViewer } from "../widgets/outputs";
export class CertsPage extends React.Component {
  constructor() {
    super()
  }

  componentDidMount() {
    this.on_load_certs()
  }

  on_view_cert = () => {
    let cert_action = <CertAction mode='view' item={cert_table_controller.get_selected_item()} />
    modal_controller.show(cert_action)
  }

  on_delete_cert = () => {
    let cert_action = <CertAction mode='delete' item={cert_table_controller.get_selected_item()} />
    modal_controller.show(cert_action)
  }

  on_revoce_cert = () => {
    let cert_action = <CertAction mode='revoce' item={cert_table_controller.get_selected_item()} />
    modal_controller.show(cert_action)
  }


  on_load_certs = () => {
    console.log("CertsPage.on_load_certs")
    cert_table_controller.update_content()
  }

  on_export = () => {
    let item = cert_table_controller.get_selected_item()
    let cert_text = "-----BEGIN CERTIFICATE-----\n" + item.text + "\n-----END CERTIFICATE-----"
    var myblob = new Blob([cert_text], {
      type: 'text/plain'
    });
    let url = window.URL.createObjectURL(myblob);
    let a = document.createElement('a');
    a.href = url;
    a.download = item.subject_name + '.cer';
    a.click();
  }

  get_button_panel = () => {
    let result = []
    result.push(<button onClick={this.on_load_certs}>{locales.get("UpdateTable")}</button>)
    let available_actions = cert_table_controller.get_available_actions()
    console.log("CertsPage.get_button_panel", available_actions)
    for (let i in available_actions) {
      let action_name = available_actions[i]
      switch (action_name) {
        case "view": {
          result.push(<button onClick={this.on_view_cert}>{locales.get(action_name)}</button>)
          break
        }
        case "delete": {
          result.push(<button onClick={this.on_delete_cert}>{locales.get(action_name)}</button>)
          break
        }
        case "revoce": {
          result.push(<button onClick={this.on_revoce_cert}>{locales.get(action_name)}</button>)
          break
        }
        case "export": {
          result.push(<button onClick={this.on_export}>{locales.get(action_name)}</button>)
          break
        }
      }
    }

    return <div className="ActionPanel">{result}</div>
  }



  render() {
    return (<div>
      <TimeIntervalSelector
        onUpdate={this.on_load_certs} />
      <FiltersLayerWidget
        table_controller={cert_table_controller}
        selector_options={["created", "expired", "revoced"]}
      />
      {this.get_button_panel()}
      <CustomTable
        keys={cert_table_controller.get_keys()}
        content={cert_table_controller.get_content()}
        marks={cert_table_controller.get_selected_idxs()}
        on_item_select={cert_table_controller.on_select}
      />
      <CustomTablePagingButtons
        current_page={cert_table_controller.get_current_page()}
        on_prev_page={cert_table_controller.on_prev_page}
        on_next_page={cert_table_controller.on_next_page}
      />
    </div>)
  }
}

class CertEditor extends React.Component {
  constructor() {
    super()
    this.state = {
      subject_name: "",
      algorithm: "gostr34102012-gostr34112012-256",
      pin: "11111111"
    }
  }

  onError = (error_description) => {
    let error_message = <ErrorMessage code={error_description["code"]} message={error_description["message"]} />
    modal_controller.show(error_message)
  }

  on_create = () => {
    finfal_rc.post_cert(this.state.subject_name,
      this.state.algorithm,
      this.state.pin).then((result) => {
        modal_controller.hide()
        cert_table_controller.update_content()
      })
  }

  on_close = () => {
    modal_controller.hide()
  }



  get_body = () => {
    return <div>
      <InputString label="subject_name" validation={subject_name_validation} value={this.state.subject_name} onChange={(value) => { this.setState({ "subject_name": value }) }} />
      <InputString label="algorithm" value={this.state.algorithm} onChange={(value) => { this.setState({ "algorithm": value }) }} />
      <InputInt label="pin" value={this.state.pin} onChange={(value) => { this.setState({ "pin": value }) }} />
    </div>
  }

  get_buttons = () => {
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_create}>{locales.get("create")}</button>
    </div>

  }

  render() {
    return <div>
      {this.get_body()}
      {this.get_buttons()}
    </div>
  }
}

class CertAction extends ActionWrapper {
  on_close = () => {
    modal_controller.hide()
  }

  get_action_subwidget = () => {
    switch (this.props.mode) {
      case "view": {
        return <CertActionView />
      }
      case "delete": {
        return <CertActionDelete item={this.props.item} />
      }
      case "revoce": {
        return <CertRevoce item={this.props.item} />
      }
    }
  }

  render() {
    return (<div className="CertAction">
      <CertViwer item={this.props.item} />
      {this.get_action_subwidget()}
    </div>)
  }
}

class CertActionTemplate extends ActionWrapper {
  on_close = () => {
    modal_controller.hide()
  }

  get_buttons_panel = () => {
    return <div className="modal_action_btn_panel"><button onClick={this.on_close}>{locales.get("close")}</button></div>
  }
  get_action_text = () => {

  }
  render() {
    return (<div className="CertActionTemplate">
      {this.get_action_text()}
      {this.get_buttons_panel()}
    </div>)
  }
}

class CertActionView extends CertActionTemplate {

}

class CertActionDelete extends CertActionTemplate {
  on_delete = () => {
    finfal_rc.delete_cert(this.props.item.id)
      .then((result) => {
        modal_controller.hide()
        cert_table_controller.update_content()
      }).catch((data) => {
        console.log(data["content"])
        this.onError(data["content"])
      })
  }

  get_buttons_panel = () => {
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_delete}>{locales.get("delete")}</button>
    </div>
  }

  get_action_text = () => {
    return <div className="modal_action_question_block"><label>{locales.get("cert_deletion_question")}</label></div>
  }
}

class CertRevoce extends CertActionTemplate {
  constructor() {
    super()
    this.state = {
      "revocation_reason": ""
    }
  }

  on_revoce = () => {
    finfal_rc.set_cert_status(this.props.item.id, "revoced", this.state.revocation_reason)
      .then((result) => {
        modal_controller.hide()
        cert_table_controller.update_content()
      }).catch((data) => {
        console.log(data["content"])
        this.onError(data["content"])
      })
  }

  get_role_selector = () => {

  }

  get_action_text = () => {
    return <div className="modal_action_question_block">
      <label>{locales.get("cert_revocation_question")}</label>
      <InputString label={locales.get("revocation_reason")}
        onChange={(value) => {
          this.setState({ "revocation_reason": value })
        }} />
    </div>
  }

  get_buttons_panel = () => {
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_revoce}>{locales.get("revoce_cert_btn")}</button>
    </div>
  }
}


export class CertViwer extends React.Component {
  constructor() {
    super()
    this.state = {
      issuer_name: "",
      subject_name: "",
      not_before: "",
      not_after: "",
      pkey_algorithm: "",
      pkey_value: "",
      skey_validity: "",
      key_usage: [],
      exts: {},
    }
  }

  componentDidMount() {
    extsx509v3_controller.upload_from_item(this.props.item.exts)
    this.setState({
      issuer_name: this.props.item.issuer_name,
      subject_name: this.props.item.subject_name,
      pkey_algorithm: this.props.item.pkey_algorithm,
      pkey_value: this.props.item.pkey_value,
      skey_validity: this.props.item.skey_validity,
      key_usage: this.props.item.key_usage,
    })
  }

  render() {
    return <div>
      <InputString disabled={true} label="issuer_name" value={this.state.issuer_name} />
      <InputString disabled={true} label="subject_name" value={this.state.subject_name} />
      <InputString disabled={true} label="pkey_algorithm" value={this.state.pkey_algorithm} />
      <PKeyViewer value={this.state.pkey_value} />
      <KeyUsageViewer key_usage={this.state.key_usage} />
      <ExtsX509v3_constructor mode={"viewer"} />
    </div>
  }

}