//all validators should return:
// {'state':book, "description": str}


export function subject_name_validation(input_s) {
    if (input_s == null) return { "state": true, "description": "" }
    try {
        let possible_keys = ["CN", "G", "SN", "I", "E", "INN", "OGRN", "OGRNIP", "SNILS", "C", "S", "L", "Street", "O", "OU", "T", "INNLE", "IntGov"]
        let fragments = input_s.split(';')

        for (let i in fragments) {
            let fragment = fragments[i]
            if (fragment.length != 0) {
                let key_arg = fragment.split('=')
                let key = key_arg[0]
                let arg = key_arg[1]
                if (!possible_keys.includes(key)) {
                    return { "state": false, "description": "not appropriate key" }
                }
                if (arg.length < 1) {
                    return { "state": false, "description": "no input value" }
                }
            }
        }
        return { "state": true, "description": "" }
    }
    catch (e) {
        return { "state": false, "description": "invalid input format" }
    }
}

export function pin_validation(input_s) {
    if (input_s != null)
        if (input_s.length >= 8) {
            return { "state": true, "description": "" }
        }
    return { "state": false, "description": "too short pin_code" }

}

export function cant_be_empty(input_s) {
    if (input_s != null)
        if (input_s.length > 0) {
            return { "state": true, "description": "" }
        }
    return { "state": false, "description": "cant be empty" }
}
