import { store } from "../storage/storage"
import { finfal_rc } from "../backend_api";
import { registerTable } from "../storage/storage";
import { createTableSlice } from "../storage/slices/tableSlice";
import { addTableReducer } from "../storage/storage";
import { CommonTableController } from "./common_table_controller";

class ScertTableController extends CommonTableController{
    constructor(){
        super("scerts")
        this.set_filters({
            "sname_filter":null,
        })
    }
    get_keys = () =>{
        return ["id", "subject_name", "is_active", "is_pin_active", "created_at"]
    }

    get_content(){
        let data = super.get_content()
        for(let i in data){
            let item = data[i]
            item["is_active"] =  item["is_active"]?"true":"false"
            item["is_pin_active"] =  item["is_pin_active"]?"true":"false"   
        }
        return data
    }


    on_select = (item) =>{
        finfal_rc.get_scert_by_id(item.id).then((item)=>{store.dispatch(this.actions.select_item(item))})
    }

    update_content = () =>{
        finfal_rc.get_scerts(
            this.filters["sname_filter"],
            this.filters["offset"],
            this.filters["count"],
        ).then((data) => {
            store.dispatch(this.actions.set_content(data))
          })
    }

    get_available_actions = () =>{
        let selected_item = this.get_selected_item()
        if (selected_item==null) return []
        if (!("id" in selected_item)) return []
        return ["view", "edit", "delete", "make_active", "set_pin", "export"]
    }
}

export const scert_table_controller = new ScertTableController()