import React from "react"
import '../../styles/output.css'
import { locales } from "../../static/locales"
export class StringViewer extends React.Component{    
    render(){
        return <div>
            <label>{this.props.label}</label><label>{this.props.value}</label>
        </div>
    }
}

export class PKeyViewer extends React.Component{
    get_pkey_multiline = () =>{
        let result = ""
        let length = this.props.value.length
        let crop_size =  64
        for(let i =0; i<length/crop_size; i++){
            result = result+" "+this.props.value.substr(i*crop_size, crop_size)
        }
        return result
    }
    render(){
        return <div className="PKeyViewer">
            <label className="pkey_label">{locales.get("pkey_value")}</label>
            <label className="pkey_content">{this.get_pkey_multiline()}</label>
        </div>
    }
}

export class KeyUsageViewer extends React.Component{
    get_key_usage_list = () =>{
        let result = []
        for(let i in this.props.key_usage){
            if (this.props.key_usage[i])
            result.push(<label>{this.props.key_usage[i]}</label>)
        }
        return <div className = "KeyUsageViewer_block">{result}</div>
    }
    render(){
        return <div className="KeyUsageViewer">
            <label>{locales.get("key_usage_label")}: </label>
            {this.get_key_usage_list()}
        </div>
    }
}