import { store } from "../storage/storage"
import { set_active, set_params,create_ext, clear_exts } from "../storage/slices/extsSlice"
class ExtsX509v3_controller {
    constructor() {
        this.exts = store.getState().exts
        this.unsubscribe = store.subscribe(() => {
            this.exts = store.getState().exts
        })
    }

    clear() {
        let exts_description = Object.assign({}, this.exts)
        for (let ext_name in exts_description) {
            store.dispatch(set_active(
                {
                    "ext_name": ext_name,
                    "is_active": false
                }
            ))
        }
    }

    upload_from_item(exts_item) {
        console.log("upload_from_item", exts_item)
        
        store.dispatch(clear_exts(1))
        let exts_description = JSON.parse(JSON.stringify(this.exts))
        console.log("upload_from_item2", exts_description)
        for (let ext_name in exts_item) {
            let ext_params = exts_item[ext_name]
            store.dispatch(set_params(
                {
                    "ext_name": ext_name,
                    "ext_params": ext_params
                }
            ))
            store.dispatch(set_active(
                {
                    "ext_name": ext_name,
                    "is_active": true
                }
            ))
        }
    }

    export_to_item() {
        let exts_description = Object.assign({}, this.exts)
        console.log("export_to_item", exts_description)
        let exts_item = {}
        for (let ext_name in exts_description) {
            let ext_active = exts_description[ext_name]["is_active"]
            let ext_params = exts_description[ext_name]["params"]
            if (ext_active) {
                exts_item[ext_name] = ext_params
            }
        }
        return exts_item
    }

    set_active(ext_name, state) {
        this.create_if_not_exists(ext_name)
        store.dispatch(set_active(
            {
                "ext_name": ext_name,
                "is_active": state
            }
        ))
    }

    is_active(ext_name) {
        this.create_if_not_exists(ext_name)
        let exts_description = JSON.parse(JSON.stringify(this.exts))
        return exts_description[ext_name]["is_active"]     
    }

    create_if_not_exists(ext_name){
        let exts_description = JSON.parse(JSON.stringify(this.exts))
        if (exts_description[ext_name]==null){
            store.dispatch(create_ext(ext_name))
        }
    }
    

    set_params(ext_name, ext_params) {
        this.create_if_not_exists(ext_name)
        store.dispatch(set_params(
            {
                "ext_name": ext_name,
                "ext_params": ext_params
            }
        ))
    }

    init_if_not_exists(ext_name, template){
        let exts_description = JSON.parse(JSON.stringify(this.exts))
        if (exts_description[ext_name]==null){
            store.dispatch(create_ext(ext_name))
        }
    }

    get_param_by_key(ext_name, key){
        console.log(ext_name, key)
        this.create_if_not_exists(ext_name)
        let exts_description = this.get_params(ext_name)
        return exts_description[key]
    }

    update_params(ext_name, key, value){
        this.create_if_not_exists(ext_name)
        let exts_description = this.get_params(ext_name)
        exts_description[key] = value
        this.set_params(ext_name, exts_description)
    }

    get_params(ext_name){
        this.create_if_not_exists(ext_name)
        let exts_description = JSON.parse(JSON.stringify(this.exts))
        return exts_description[ext_name]["params"]
    }
}


export const extsx509v3_controller = new ExtsX509v3_controller()