export class CrlsPage extends React.Component {
  constructor() {
    super()
  }

  componentDidMount() {
    this.on_load_crls()
  }

  on_view_crl = () => {
    let crl_action = <crlAction mode='view' item={crl_table_controller.get_selected_item()} />
    modal_controller.show(crl_action)
  }

  on_delete_crl = () => {
    let crl_action = <crlAction mode='delete' item={crl_table_controller.get_selected_item()} />
    modal_controller.show(crl_action)
  }

  on_make_active_crl = () => {
    let crl_action = <crlAction mode='make_active' item={crl_table_controller.get_selected_item()} />
    modal_controller.show(crl_action)
  }

  on_post_crl = () => {
    finfal_rc.post_crl().then(crl_table_controller.update_content())
    
  }

  on_load_crls = () => {
    console.log("crlsPage.on_load_crls")
    crl_table_controller.update_content()
  }

  on_export = () =>{
      let item = crl_table_controller.get_selected_item()
      let cert_text = "-----BEGIN CRL-----\n" + item.text + "\n-----END CRL-----"
              var myblob = new Blob([cert_text], {
                  type: 'text/plain'
              });
              let url = window.URL.createObjectURL(myblob);
              let a = document.createElement('a');
              a.href = url;
              a.download = item.issuer_name + '.crl';
              a.click();
    }
  

  get_button_panel = () => {
    let result = []
    result.push(<button onClick={this.on_load_crls}>{locales.get("UpdateTable")}</button>)
    result.push(<button onClick={this.on_post_crl}>{locales.get("create")}</button>)

    let available_actions = crl_table_controller.get_available_actions()
    console.log("crlsPage.get_button_panel", available_actions)
    for (let i in available_actions) {
      let action_name = available_actions[i]
      switch (action_name) {
        case "view": {
          result.push(<button onClick={this.on_view_crl}>{locales.get(action_name)}</button>)
          break
        }
        case "delete": {
          result.push(<button onClick={this.on_delete_crl}>{locales.get(action_name)}</button>)
          break
        }
        case "export": {
          result.push(<button onClick={this.on_export}>{locales.get(action_name)}</button>)
          break
        }
      }
    }

    return <div className = "ActionPanel">{result}</div>
  }

  render() {
    return (<div>
      {this.get_button_panel()}
      <CustomTable
        keys={crl_table_controller.get_keys()}
        content={crl_table_controller.get_content()}
        marks={crl_table_controller.get_selected_idxs()}
        on_item_select={crl_table_controller.on_select}
      />
      <CustomTablePagingButtons
        current_page={crl_table_controller.get_current_page()}
        on_prev_page={crl_table_controller.on_prev_page}
        on_next_page={crl_table_controller.on_next_page}
      />
    </div>)
  }
}

class crlUploader extends React.Component {
  constructor() {
    super()
    this.state = {
      crl_texts: [],
      crl_filenames: []
    }
  }

  on_upload = () => {

    for (let i in this.state.crl_texts){
      let file_content = file_encoder.get_crl_text_from_raw_filecontent(this.state.crl_texts[i])
      finfal_rc.post_crl(file_content)
    }
    modal_controller.hide()
    crl_table_controller.update_content()
  }

  on_close = () => {
    modal_controller.hide()
  }


  uploadcrlContent = (event) => {
    ////console.log("event.target.files", event.target.files,)
    let crl_texts = []
    let crl_filenames = []
    for (var i = 0; i < event.target.files.length; i++) {
      ////console.log("i", i, typeof (i))
      let reader = new FileReader()
      reader.readAsDataURL(event.target.files[i]);
      reader.onload = () => {
        crl_texts.push(reader.result)
      }
      crl_filenames.push(event.target.files[i]['name'])

    }

    this.setState({
      'crl_texts': crl_texts,
      'crl_filenames': crl_filenames
    })

  }

  get_body = () => {
    return <div>
      <input type="file" accept=".crl" onChange={this.uploadcrlContent} multiple />
    </div>
  }

  get_buttons = () => {
    return <div className = "modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_upload}>{locales.get("upload")}</button>
    </div>

  }

  render() {
    return <div>
      {this.get_body()}
      {this.get_buttons()}
    </div>
  }
}

class crlAction extends React.Component {
  on_close = () => {
    modal_controller.hide()
  }

  get_action_subwidget = () => {
    switch (this.props.mode) {
      case "view": {
        return <crlActionView />
      }
      case "delete": {
        return <crlActionDelete item={this.props.item} />
      }
      case "make_active": {
        return <crlMakeActive item={this.props.item} />
      }
    }
  }

  render() {
    return (<div className="crlAction">
      <crlViwer item={this.props.item} />
      {this.get_action_subwidget()}
    </div>)
  }
}

