class crlActionView extends crlActionTemplate {

}

class crlActionDelete extends crlActionTemplate {
  on_delete = () => {
    finfal_rc.delete_crl(this.props.item.id)
      .then((result) => {
        modal_controller.hide()
        crl_table_controller.update_content()
      }).catch((data)=>{
          console.log(data["content"])
          this.onError(data["content"])
        })
  }

  get_buttons_panel = () => {
    return <div className="modal_action_btn_panel">
      <button onClick={this.on_close}>{locales.get("close")}</button>
      <button onClick={this.on_delete}>{locales.get("delete")}</button>
    </div>
  }

  get_action_text = () => {
    return <div className="modal_action_question_block"><label>{locales.get("crl_deletion_question")}</label></div>
  }
}

