export class crlViwer extends React.Component {
  constructor() {
    super()
    this.state = {
      subject_name: "",
      pkey_algorithm: "",
      pkey_value: "",
    }
  }

  componentDidMount() {
    extsx509v3_controller.upload_from_item(this.props.item.exts)
    this.setState({
      issuer_name: this.props.item.issuer_name,
      subject_name: this.props.item.subject_name,
      pkey_algorithm: this.props.item.pkey_algorithm,
      pkey_value: this.props.item.pkey_value,
    })
  }

  render() {
    return <div>
      <StringViewer label="subject_name" value={this.state.subject_name} />
      <StringViewer label="pkey_algorithm" value={this.state.pkey_algorithm} />
      <StringViewer label="pkey_value" value={this.state.pkey_value} />
    </div>
  }

}