import { http_request, http_auth_request } from "./http_request"
import { config_loader } from "./config_loader"

export class finfal_rc {
    constructor() {
        this.token = ""
    }

    set_token = (token) => {
        this.token = token
    }

    async auth(login, password) {
        console.log("finfal_rc.auth", this.token)
        let url = config_loader.get_server_url()
        let data = await http_auth_request(url + "/auth/login", login, password)
        return data
    }

    //======================================PROFILES==================================================================

    async get_profiles(pname_filter, sname_filter, offset, count) {
        let url = config_loader.get_server_url()
        //console.log("finfal_rc.get_profiles")
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        if (pname_filter) header['pname_filter'] = pname_filter
        if (sname_filter) header['sname_filter'] = sname_filter
        if (offset != null) header['offset'] = offset
        if (count != null) header['count'] = count

        let data = await http_request(url + "/profiles", "GET", header, {})
        return data
    }

    async post_profile(profile_name, subject_name, duration, key_usage, exts) {
        let url = config_loader.get_server_url()
        let data = await http_request(url + "/profiles", "POST", { 'Authorization': `Bearer ${this.token}` }, {
            "profile_name": profile_name,
            "subject_name": subject_name,
            "duration": duration,
            "key_usage": key_usage,
            "exts": exts
        })
    }

    async delete_profile(profile_id) {
        let url = config_loader.get_server_url()
        let data = await http_request(url + `/profiles/${profile_id}`, "DELETE", { 'Authorization': `Bearer ${this.token}` }, {})
        return data
    }

    async update_profile(profile_id, profile_name, subject_name, duration, key_usage, exts) {
        let url = config_loader.get_server_url()
        let data = await http_request(url + `/profiles/${profile_id}`, "PUT", { 'Authorization': `Bearer ${this.token}` }, {
            "profile_name": profile_name,
            "subject_name": subject_name,
            "duration": duration,
            "key_usage": key_usage,
            "exts": exts
        })
        return data
    }

    async get_profile_by_id(profile_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/profiles/${profile_id}`, "GET", header, {})
        return data
    }

    async set_profile_active(profile_id, role) {
        let url = config_loader.get_server_url()
        let data = await http_request(url + `/profiles/${profile_id}/active`, "PUT", { 'Authorization': `Bearer ${this.token}` }, {
            "role": role
        })
        return data
    }


    async get_scerts(sname_filter, offset, count) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        if (sname_filter) header['sname_filter'] = sname_filter
        if (offset != null) header['offset'] = offset
        if (count != null) header['count'] = count

        let data = await http_request(url + "/signcerts", "GET", header, {})
        return data
    }

    async get_scert_by_id(scert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/signcerts/${scert_id}`, "GET", header, {})
        return data
    }

    async post_scert(subject_name, algorithm, pin) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "subject_name": subject_name,
            "algorithm": algorithm,
            "pin": pin
        }

        let data = await http_request(url + `/signcerts`, "POST", header, body)
        return data
    }


    async delete_scert(scert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/signcerts/${scert_id}`, "DELETE", header, {})
        return data
    }

    async make_scert_active(scert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/signcerts/${scert_id}/active`, "PUT", header, {})
        return data
    }

    async set_scert_pin(scert_id, pin) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "pin": pin
        }

        let data = await http_request(url + `/signcerts/${scert_id}/pin`, "PUT", header, body)
        return data
    }

    async get_csrs(not_before, not_after, sname_filter, status, offset, count) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        if (sname_filter) header['sname-filter'] = sname_filter
        if (not_before != null) header['not-before'] = not_before
        if (not_after != null) header['not-after'] = not_after
        if (status != null) 
            if (status!="")
                header['status'] = status

        if (offset != null) header['offset'] = offset
        if (count != null) header['count'] = count
        
        console.log("get_csrs", header)
        let data = await http_request(url + "/csrs", "GET", header, {})
        return data
    }

    async get_csr_by_id(scert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/csrs/${scert_id}`, "GET", header, {})
        return data
    }

    async post_csr(csr_data_b64, scert_id = null) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "csr_text": csr_data_b64,
        }
        if (scert_id) {
            body["scert_id"] = scert_id
        }

        let data = await http_request(url + `/csrs`, "POST", header, body)
        return data
    }


    async delete_csr(csr_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/csrs/${csr_id}`, "DELETE", header, {})
        return data
    }

    async get_certs(not_before, not_after, sname_filter, status, offset, count) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        if (not_before != null) header['not-before'] = not_before
        if (not_after != null) header['not-after'] = not_after
        if (sname_filter) header['sname_filter'] = sname_filter
        if (status) header['status'] = status
        if (offset != null) header['offset'] = offset
        if (count != null) header['count'] = count

        let data = await http_request(url + "/certs", "GET", header, {})
        return data
    }


    async get_cert_by_id(cert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/certs/${cert_id}`, "GET", header, {})
        return data
    }

    async set_cert_status(cert_id, new_status, reason) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "new_status": new_status,
            "reason": reason
        }

        let data = await http_request(url + `/certs/${cert_id}`, "PUT", header, body)
        return data
    }

    async delete_cert(cert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/certs/${cert_id}`, "DELETE", header, {})
        return data
    }

    async get_crls(sname_filter, offset, count) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        if (sname_filter) header['sname_filter'] = sname_filter
        if (offset != null) header['offset'] = offset
        if (count != null) header['count'] = count

        let data = await http_request(url + "/crls", "GET", header, {})
        return data
    }
    async post_crl() {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + "/crls", "POST", header, {})
        return data
    }

    async get_crl_by_id(cert_id) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/crls/${cert_id}`, "GET", header, {})
        return data
    }

    async get_ca_status() {
        let url = config_loader.get_server_url()
        console.log("get_ca_status", url)
        let data = await http_request(url + `/utils/status`, "GET", {}, {})
        return data
    }

    async get_ca_config() {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let data = await http_request(url + `/utils/config`, "GET", header, {})
        return data
    }

    async post_ca_initiate(username, password) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "username": username,
            "password": password
        }
        let data = await http_request(url + `/users/initiate`, "POST", header, body)
        return data
    }


    async post_ca_config(config) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = config
        let data = await http_request(url + `/utils/config`, "POST", header, body)
        return data
    }

    async post_new_password(old_password, new_password) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "new_password": new_password,
            "old_password": old_password
        }
        console.log("post_new_password", body)
        let data = await http_request(url + `/users/my_account/password`, "POST", header, body)
        return data

    }


    async get_users() {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + "/users", "GET", header, {})
        return data
    }
    async post_user(login, password) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "login": login,
            "password": password
        }

        let data = await http_request(url + "/users", "POST", header, body)
        return data
    }

    async get_user_by_login(login) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/users/${login}`, "GET", header, {})
        return data
    }

    async delete_user(login) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        let data = await http_request(url + `/users/${login}`, "DELETE", header, {})
        return data
    }

    async put_user_activity(login, state) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "is_active": state,
        }

        let data = await http_request(url + `/users/${login}/set_activity`, "PUT", header, body)
        return data
    }

    async set_user_role(login, role) {
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }
        let body = {
            "role": role,
        }

        let data = await http_request(url + `/users/${login}/set_role`, "PUT", header, body)
        return data
    }



    async get_logs(not_before, not_after, user, action, result, offset, count){
        let url = config_loader.get_server_url()
        let header = {
            'Authorization': `Bearer ${this.token}`,
        }

        if (not_before != null) header['not-before'] = not_before
        if (not_after != null) header['not-after'] = not_after
        if (user != null) header['user'] = user
        if (action != null) header['action'] = action
        if (result != null) header['result'] = result
        if (offset != null) header['offset'] = offset
        if (count != null) header['count'] = count

        console.log("get_logs", header)
        let data = await http_request(url + `/utils/logs`, "GET", header, {})
        return data
    }


}


export const finfal_rc = new finfal_rc("http://127.0.0.1:10000")