export class ExtsX509v3_constructor extends React.Component {
    constructor() {
        super()
    }

    render() {
        return <div className="ExtsX509v3_constructor">
            <b>{locales.get("ExtsX509v3_constructor")}</b>
            <ExtsX509v3_privateKeyUsagePeriod mode={this.props.mode} />
            <ExtsX509v3_ECPmethods mode={this.props.mode} />
            <ExtsX509v3_applicationCertPolicies mode={this.props.mode} />
            <ExtsX509v3_authorityInfoAccess mode={this.props.mode} />
            <ExtsX509v3_basicConstraints mode={this.props.mode} />
            <ExtsX509v3_certificatePolicies mode={this.props.mode} />
            <ExtsX509v3_certificateTemplate mode={this.props.mode} />
            <ExtsX509v3_cRLDistributionPoints mode={this.props.mode} />
            <ExtsX509v3_extKeyUsage mode={this.props.mode} />
            <ExtsX509v3_DocumentType mode={this.props.mode} />
            <ExtsX509v3_identidicationKind mode={this.props.mode} />
            <ExtsX509v3_signTool mode={this.props.mode} />
            <ExtsX509v3_subjectAltName mode={this.props.mode} />
            <ExtsX509v3_issuerAltName mode={this.props.mode} />
        </div>
    }
}


class ExtsX509v3_BasicExtensionTemplate extends React.Component {
    constructor() {
        super()
        this.state = {
            "name": "privateKeyUsagePeriod",
            exts: store.getState().exts,
        }
    }

    componentDidMount() {
        this.unsubscribe = store.subscribe(() => {
            this.setState({
                exts: store.getState().exts,
            });
        });

    }

    on_activate = (state) => {
        extsx509v3_controller.set_active(this.state.name, state)
    }

    is_active = () => {
        return extsx509v3_controller.is_active(this.state.name)
    }

    get_activator = () => {
        return <InputCheckbox
            add_classname="ExtsX509v3_activator"
            value={extsx509v3_controller.is_active(this.state.name)}
            onChange={(state) => { extsx509v3_controller.set_active(this.state.name, state) }} />
    }

    get_label = () => {
        return <label>{locales.get(this.state.name)}</label>
    }

    get_param(key) {
        return extsx509v3_controller.get_param_by_key(this.state.name, key)
    }

    on_params_change(key, value) {
        extsx509v3_controller.update_params(this.state.name, key, value)
    }

    get_params_block = () => {
        let disabled = !this.is_active()
        if (this.props.mode == "viewer") {
            disabled = true
        }
        return <div>
            <InputInt
                disabled={disabled}
                label={locales.get("privateKeyUsagePeriod_duration")}
                value={this.get_param("duration")}
                onChange={(value) => { this.on_params_change("duration", value) }}
            />
        </div>
    }

    render() {
        if (this.props.mode == "viewer") {
            if (this.is_active()) {
                return <div className="ExtsX509v3_ext_viewer">
                    {this.get_label()}
                    {this.get_params_block()}
                </div>
            }
            else {
                return <span></span>
            }

        }
        return <div className="ExtsX509v3_ext">
            {this.get_activator()}
            {this.get_label()}
            {this.get_params_block()}
        </div>
    }
}

class ExtsX509v3_privateKeyUsagePeriod extends ExtsX509v3_BasicExtensionTemplate { }

