class ExtsX509v3_certificatePolicies extends ExtsX509v3_BasicExtensionTemplate {
    constructor() {
        super()
        this.state = {
            "name": "certificatePolicies",
            exts: store.getState().exts,
        }
    }
    set_qualifier = (quilifier_name, value) => {
        let qualifiers = this.get_param("qualifiers")
        if (qualifiers == null) {
            qualifiers = {
                "qualifier_1": "",
                "qualifier_2": ""
            }
        }
        qualifiers[quilifier_name] = value
        this.on_params_change("qualifiers", qualifiers)
    }

    get_policies_list = () => {
        return ["1.2.643.100.113.1", "1.2.643.100.113.2",
            "1.2.643.100.113.3", "1.2.643.100.113.4",
            "1.2.643.100.113.5"
        ]
    }

    set_policies = (policies_list) => {
        let policies_keys = this.get_policies_list()
        let result = {}
        for (let i in policies_keys) {
            if (policies_list.includes(policies_keys[i]))
                result[policies_keys[i]] = true
            else
                result[policies_keys[i]] = false
        }
        this.on_params_change("policies", result)
    }

    get_current_policies = () => {
        let policies_dict = extsx509v3_controller.get_param_by_key(this.state.name, "policies")
        let result = []
        for (let k in policies_dict) {
            if (policies_dict[k])
                result.push(k)
        }
        return result
    }

    get_param_from_section = (section_name, key_value) => {
        let section = extsx509v3_controller.get_param_by_key(this.state.name, section_name)
        console.log("get_param_from_section", section_name, key_value, section)
        if (section == null) return null
        return section[key_value]
    }

    get_params_block = () => {
        let disabled = !this.is_active()
        if (this.props.mode == "viewer") {
            disabled = true
        }
        let result = [<label>{locales.get("certificatePolicies_qualifiers_block")}</label>]
        let input_fields = ["qualifier_1", "qualifier_2"]
        for (let i in input_fields) {
            let key_value = input_fields[i]
            result.push(<InputString
                disabled={disabled}
                label={locales.get(key_value)}
                value={this.get_param_from_section("qualifiers", key_value)}
                onChange={(value) => { this.set_qualifier(key_value, value) }}
            />)
        }
        result.push(<label>{locales.get("certificatePolicies_policies_block")}</label>)
        input_fields = this.get_policies_list()

        result.push(<SelectorFilter
            label = {'policies'}
            value={this.get_current_policies()}
            onChange={this.set_policies}
            options={input_fields} />)
        /*for (let i in input_fields) {
            let key_value = input_fields[i]
            result.push(<InputCheckbox
                disabled={disabled}
                label={locales.get(key_value)}
                value={this.get_param_from_section("policies", key_value)}
                onChange={(value) => { this.set_policy(key_value, value) }}
            />)
        }*/

        return <div>
            {result}
        </div>
    }
}

