//"values": [str]
class ExtsX509v3_cRLDistributionPoints extends ExtsX509v3_BasicExtensionTemplate {
    constructor() {
        super()
        this.state = {
            "name": "cRLDistributionPoints",
            exts: store.getState().exts,
        }
    }

    on_delete_item = (idx) => {
        let values = this.get_param("values")
        console.log("on_delete_item", values, idx)
        if (!values) values = []
        values.splice(idx, 1)
        this.on_params_change("values", values)

    }

    on_add_item = () => {
        let values = this.get_param("values")
        if (!values) values = []
        values.push("")
        this.on_params_change("values", values)
    }

    on_change_item = (idx, value) => {
        let values = this.get_param("values")
        values[idx] = value
        this.on_params_change("values", values)
    }

    get_params_block = () => {
        let disabled = !this.is_active()
        if (this.props.mode == "viewer") {
            disabled = true
        }

        let values = this.get_param("values")
        let result = []

        for (let i in values) {
            let value = values[i]
            let input_string = <InputString
                disabled={disabled}
                label={locales.get("")}
                value={value}
                onChange={(value) => { this.on_change_item(i, value) }}
            />
            if (!disabled) {
                result.push(<div className="ExtsX509v3_array_block"> {input_string}
                    <button onClick={(e) => { this.on_delete_item(i) }}>x</button>
                </div>)
            }
            else {
                result.push(<div> {input_string} </div>)
            }
        }
        if (!disabled) {
            result.push(<button onClick={(e) => { this.on_add_item() }}>+</button>)
        }
        return <div>
            {result}
        </div>
    }
}

