/*
    name = "subjectAltName"
    oid = "2.5.29.17"

    #     
    # params = {
    # "altnames":[
    #    {
    #        "rfc822Name":str,
    #    },
    #    {
    #        "dNSName": str,
    #    },
    #    {
    #        "uniformResourceIdentifier": str,
    #    },
    #    {
    #       "id_at_countryName": str,
    #       "id_at_stateOrProvinceName": str,
    #       "id_at_localityName": str,
    #       "id_at_organizationName": str,
    #       "id_at_commonName": str,
    #    }
    #
    #]
    #
    #  }
*/

class ExtsX509v3_subjectAltName extends ExtsX509v3_BasicExtensionTemplate {
    constructor() {
        super()
        this.state = {
            "name": "subjectAltName",
            exts: store.getState().exts,
        }
    }

    on_delete_item = (idx) => {
        let values = this.get_param("altnames")
        console.log("on_delete_item", values, idx)
        if (!values) values = []
        values.splice(idx, 1)
        this.on_params_change("altnames", values)

    }

    on_add_item = (elem_type) => {
        let values = this.get_param("altnames")
        if (!values) values = []
        switch (elem_type) {
            case 'rfc822Name': {
                values.push({
                    "rfc822Name": "",
                })
                break
            }
            case 'dNSName': {
                values.push({
                    "dNSName": "",
                })
                break
            }
            case 'uniformResourceIdentifier': {
                values.push({
                    "uniformResourceIdentifier": "",
                })
            }
            case 'id_at_countryName': {
                values.push({
                    "id_at_countryName": "",
                    "id_at_stateOrProvinceName": "",
                    "id_at_localityName": "",
                    "id_at_organizationName": "",
                    "id_at_commonName": "",
                })
                break
            }
        }
        this.on_params_change("altnames", values)
    }

    on_change_item = (idx, key, value) => {
        let values = this.get_param("altnames")
        values[idx][key] = value
        this.on_params_change("altnames", values)
    }

    get_item_view = (disabled, idx, item) => {
        if ("rfc822Name" in item) {
            return <InputString
                disabled={disabled}
                label={locales.get("rfc822Name")}
                value={item["rfc822Name"]}
                onChange={(value) => { this.on_change_item(idx, "rfc822Name", value) }}
            />
        }
        else if ("dNSName" in item) {
            return <InputString
                disabled={disabled}
                label={locales.get("dNSName")}
                value={item["dNSName"]}
                onChange={(value) => { this.on_change_item(idx, "dNSName", value) }}
            />
        }
        else if ("uniformResourceIdentifier" in item) {
            return <InputString
                disabled={disabled}
                label={locales.get("uniformResourceIdentifier")}
                value={item["uniformResourceIdentifier"]}
                onChange={(value) => { this.on_change_item(idx, "uniformResourceIdentifier", value) }}
            />
        }
        else if ("id_at_countryName" in item) {
            let result = []
            let id_at_keys = ["id_at_countryName",
                "id_at_stateOrProvinceName",
                "id_at_localityName",
                "id_at_organizationName",
                "id_at_commonName"]

            for (let j in id_at_keys) {
                let key_label = id_at_keys[j]
                result.push(<InputString
                    disabled={disabled}
                    label={locales.get(key_label)}
                    value={item[key_label]}
                    onChange={(value) => { this.on_change_item(idx, key_label, value) }}
                />)
            }
            return <div className="id_at_countryName_block">{result}</div>

        }
    }


    get_params_block = () => {
        let disabled = !this.is_active()
        if (this.props.mode == "viewer") {
            disabled = true
        }

        let values = this.get_param("altnames")
        let result = []

        for (let i in values) {
            let value = values[i]
            let input_data = this.get_item_view(disabled, i, value)
            if (!disabled) {
                result.push(<div className="ExtsX509v3_array_block"> {input_data}
                    <button onClick={(e) => { this.on_delete_item(i) }}>x</button>
                </div>)
            }
            else {
                result.push(<div> {input_data} </div>)
            }
        }
        if (!disabled) {
            result.push(<button onClick={(e) => { this.on_add_item("rfc822Name") }}>{locales.get("add_rfc822Name")}</button>)
            result.push(<button onClick={(e) => { this.on_add_item("dNSName") }}>{locales.get("add_dNSName")}</button>)
            result.push(<button onClick={(e) => { this.on_add_item("uniformResourceIdentifier") }}>{locales.get("add_uniformResourceIdentifier")}</button>)
            result.push(<button onClick={(e) => { this.on_add_item("id_at_countryName") }}>{locales.get("add_id_at_countryName")}</button>)
        }
        return <div>
            {result}
        </div>
    }
}


